<template>
  <div class="audit">
    <div class="title">
      <img src="@/assets/images/avedex-logo-single.png" alt="" />
      <van-search
        class="search"
        v-model="id"
        placeholder="搜索Token"
        @input="search"
      />
    </div>
    <div class="content">
      <div v-if="tableData.length > 0">
        <div class="item" v-for="(row, index) in tableData" :key="index">
          <div class="flex-between">
            <span>{{ row.updated_at ? $f.formatDate(row.updated_at) : '--' }}</span>
            <span v-if="row.is_audit == 1" style="color: #12b886">审核通过</span>
            <span v-else type="danger" effect="dark" style="color: #f6465d">审计失败</span>
          </div>
          <div class="flex-between mt-10">
            <div class="flex-start">
              <div class="relative mr-8">
                <van-image
                  class="icon-token"
                  round
                  width="36px"
                  height="36px"
                  icon-size="36px"
                  show-loading
                  :src="row.logo_url ? domain + row.logo_url : $f.formatIcon(row.token, row.chain)"
                  :loading-icon="require('@/assets/images/icon-default.png')"
                  :error-icon="require('@/assets/images/icon-default.png')"
                />
                <img
                  v-if="row?.chain"
                  class="icon-svg icon-network"
                  :src="`${domain}chain/${row?.chain}.png`"
                  alt=""
                  srcset=""
                />
              </div>
              <div>
                <span class="name">{{ row?.symbol || '--' }}</span>
                <div class="bottom">
                  <span>
                    {{ row?.token?.slice(0, 6) + '***' + row?.token?.slice(-4) }}
                  </span>
                  <i class="iconfont icon-fuzhi ml-5" v-copy="row.token"></i>
                </div>
              </div>
            </div>
          </div>
          <div class="flex-between mt-10">
            <span class="remark ellipsis">备注：{{ row.remark || '--' }}</span>
          </div>
        </div>
      </div>
      <van-empty v-else description="暂无数据" />

      <a href="" class="add" @click.stop.prevent="showAdd = true">
        <van-icon name="plus" />
        <span>审计</span>
      </a>
    </div>
  </div>
  <van-dialog
    v-model:show="showAdd"
    title="添加审计"
    :showConfirmButton="false"
    closeOnClickOverlay
    style="background-color: #fff; padding: 0 16px"
  >
    <van-form @submit="addSafe">
      <div class="in-item">
        <span>选择公链</span>
        <div @click="showChain = true">
          <img
            class="icon-svg icon-network"
            :src="`${domain}chain/${form.chain}.png`"
            alt=""
            srcset=""
          />
          {{ form.chain }}
          <van-icon name="arrow" size="14" color="#D2D2D2" />
        </div>
      </div>
      <div class="in-item">
        <van-field
          label="合约地址"
          v-model="form.token"
          name="token"
          placeholder="输入备注"
          style="background: #f8f8f8"
        />
      </div>
      <div class="in-item">
        <van-field
          label="备注"
          v-model="form.remark"
          name="remark"
          placeholder="输入备注"
          style="background: #f8f8f8"
        />
      </div>
      <van-button class="btn" type="primary" native-type="submit">确定</van-button>
    </van-form>
  </van-dialog>
  <van-action-sheet
    v-model:show="showChain"
    cancel-text="取消"
    description="选择链"
    teleport="#app"
    close-on-click-action
  >
    <div style="height: 50vh; overflow-y: auto; text-align: left">
      <van-cell
        v-for="item in chainList"
        :key="item.net_name"
        class="list-item"
        @click.stop="selectChain(item)"
        :title="item.name"
        :icon="$f.formatIcon(item)"
        :value="item.net_name"
        clickable
        :center="false"
      >
        <template #icon>
          <img
            v-if="item.net_name"
            class="icon-svg icon-net-connect"
            style="cursor: default"
            :src="`${domain}chain/${item.net_name}.png`"
            alt=""
            srcset=""
          />
        </template>
      </van-cell>
    </div>
  </van-action-sheet>
</template>

<script>
import { getSafeList, addSafe, getChainList } from '@/api/index.js'
export default {
  name: 'Safe',
  data() {
    return {
      tableData: [],
      domain: 'https://www.logofacade.com/',
      form: {
        token: '',
        chain: 'bsc',
        remark: 'yep'
      },
      rules: {
        token: [{ required: true, message: '必填项', trigger: 'blur' }]
      },
      isSearch: false,
      showAdd: false,
      addLoading: false,
      chainList: [],
      showChain: false,
      id: '',
      dataLoading: false
    }
  },
  mounted() {
    this.getSafeList()
    this.getChainList()
  },
  methods: {
    search(val) {
      if (this.Timer) {
        clearTimeout(this.Timer)
      }
      this.Timer = setTimeout(() => {
        this.tableData = []
        this.isSearch = true
        if (val) {
          this.getSafeList(this.id)
        } else {
          this.getSafeList()
        }
      }, 1000)
    },
    getSafeList(id) {
      this.dataLoading = true
      getSafeList({ token_id: id })
        .then(res => {
          console.log('-----tableData---------', res)
          this.tableData = res
        })
        .catch(() => {
          this.tableData = []
        })
        .finally(() => {
          this.dataLoading = false
        })
    },
    addSafe() {
      if (this.form.token !== '' && /^0x[0-9a-zA-Z]{40}$/.test(this.form.token)) {
        this.addLoading = true
        addSafe(this.form)
          .then(res => {
            this.$notify({ type: 'success', message: '添加成功' })
            this.showAdd = false
            this.getSafeList()
          })
          .catch(err => {
            console.log(err)
            this.$notify({ type: 'danger', message: err })
          })
          .finally(() => {
            this.addLoading = false
          })
      } else {
        this.$notify({ type: 'danger', message: '请输入合法地址' })
      }
    },
    getChainList() {
      getChainList()
        .then(res => {
          this.chainList = res || []
        })
        .catch(() => {
          this.chainList = []
        })
        .finally(() => {})
    },
    selectChain(item) {
      this.showChain = false
      this.form.chain = item.net_name
    }
  }
}
</script>

