<script>
import { getTokenInfo, updateTokenInfo, uploadLoadIcon, getChainList } from '@/api/index.js'
const initForm = {
  token: '',
  chain: '',
  risk_level: 0,
  risk_info: {
    'zh-cn': '',
    'zh-tw': '',
    en: ''
  },
  name_space: {
    symbol: '',
    token_name: '',
    total: '',
    website: '',
    white_paper: '',
    chain_explore: ''
  },
  community_space: {
    email: '',
    blog: '',
    reddit: '',
    slack: '',
    facebook: '',
    twitter: '',
    bitcointalk: '',
    github: '',
    telegram: '',
    qq: '',
    linkedin: '',
    discord: '',
    btok: ''
  },
  description: '',
  intro_cn: '',
  intro_en: '',
  opening_at: 0
}
export default {
  name: 'TokenInfo',
  data() {
    return {
      formSearch: {
        address: '',
        chain: 'bsc'
      },
      form: initForm,
      rules: {
        address: [{ required: true, message: '请输入token地址', trigger: 'blur' }],
        chain: [{ required: true, message: '请输入链', trigger: 'blur' }],
        time: [{ required: true, message: '请输入时间', trigger: 'blur' }],
        fee: [{ required: true, message: '请输入费用', trigger: 'blur' }],
        token_index: [{ required: true, message: '请输入index', trigger: 'blur' }]
      },
      tableData: [],
      dataLoading: false,
      loading: {},
      curItem: {},
      dialogTitleType: 1,
      addLoading: false,
      updateLoading: false,
      profile_picture: null,
      fileUrl: '',
      dialogVisible: false,
      form1: {
        profile_picture: null
      },
      rules1: {
        profile_picture: [{ required: true, message: '请上传图片', trigger: 'blur' }]
      },
      imageKey: 0,
      Timer: null,
      showStartTime: false,
      datetime: '',
      domain: 'https://www.logofacade.com/',
      chainList: [],
      showChain: false,
    }
  },
  computed: {
  },
  watch: {
    tokenId() {
      if (this.$refs?.form?.resetFields) {
        this.$refs.form.resetFields()
      }
    }
  },
  mounted() {
    this.getChainList()
  },
  methods: {
    getChainList() {
      getChainList()
        .then(res => {
          this.chainList = res || []
        })
        .catch(() => {
          this.chainList = []
        })
        .finally(() => {})
    },
    confirmTime() {
      this.form.opening_at = new Date(this.datetime).getTime() / 1000
      this.showStartTime = false
    },
    search() {
      if (this.formSearch.address && this.formSearch.chain) {
        this.getTokenInfo()
      } else {
        this.$toast('请输入正确的合约地址或链')
      }
    },
    selectChain(item) {
      this.showChain = false
      this.formSearch.chain = item.net_name
    },
    getFormData(object) {
      const formData = new FormData()
      Object.keys(object).forEach(key => formData.append(key, object[key]))
      return formData
    },
    getTokenInfo() {
      this.dataLoading = true
      const tokenId = this.formSearch.address + '-' + this.formSearch.chain
      getTokenInfo(tokenId)
        .then(res => {
          const form = res || {}
          this.form = {
            ...form,
            risk_info: res?.risk_info
              ? JSON.parse(res.risk_info)
              : { 'zh-cn': '', 'zh-tw': '', en: '' }
          }
          this.datetime = new Date(form.opening_at * 1000)
        })
        .catch(() => {
          this.form = {
            risk_info: { 'zh-cn': '', 'zh-tw': '', en: '' }
          }
          this.$notify({ type: 'danger', message: '查询失败' })
        })
        .finally(() => {
          this.dataLoading = false
        })
    },
    updateTokenInfo() {
      this.dataLoading = true
      const form = {
        ...this.form,
        opening_at: Number(this.form.opening_at),
        risk_info: JSON.stringify(this.form.risk_info)
      }
      updateTokenInfo(form)
        .then(res => {
          this.$notify({ type: 'success', message: '更新成功' })
          this.getTokenInfo()
        })
        .catch(() => {
          this.$notify({ type: 'danger', message: '更新失败' })
        })
        .finally(() => {
          this.dataLoading = false
        })
    },
    beforeUpload(e) {
      const file = e.target.files[0]
      if (file.size / 1024 / 1024 > 1) {
        this.$message.error('图片大小超出限制')
        return false
      }
      this.form1.profile_picture = file
      this.fileUrl = window.URL.createObjectURL(file)
    },
    removeFile() {
      this.form1.profile_picture = null
      this.fileUrl = ''
      this.$refs.fileBanner.value = ''
    },
    uploadLoadIcon() {
      const data = new FormData()
      const file = this.form1.profile_picture
      data.append(
        'profile_picture',
        file,
        `${this.form.token}-${this.form.chain}.${file?.name?.split?.('.')[1] || 'png'}`
      )
      data.append('token_id', `${this.form.token}-${this.form.chain}`)
      this.addLoading = true

      uploadLoadIcon(data)
        .then(res => {
          this.dialogVisible = false
          this.imageKey++
          this.$message.success('上传icon成功')
        })
        .catch(() => {
          this.$message.error('上传icon失败')
        })
        .finally(() => {
          this.addLoading = false
        })
    }
  }
}
</script>
