            :placeholder="`请输入机制补充说明`"
            :rows="1"
            type="textarea"
          />
        </div>
      </div>
      <div class="line"></div>
      <div class="flex-between flex-1">
        <span class="title">地址备注</span>
        <van-icon name="arrow-up" @click="showExpand2 = false" v-if="showExpand2" />
        <van-icon name="arrow-down" @click="showExpand2 = true" v-else />
      </div>
      <div v-if="showExpand2">
        <div class="bg-gray relative" v-for="(item, index) in form.wallet_tag" :key="index">
          <div class="tag-add">
            <van-tag
              plain
              type="primary"
              @click="addWalletTag"
              v-if="index == 0"
              :loading="loadingAdd"
            >
              添加
            </van-tag>
            <van-tag
              type="danger"
              @click="removeWalletTag(index)"
              v-if="index > 0"
              :loading="loadingRemove[index]"
            >
              删除
            </van-tag>
          </div>
          <div class="flex-start">
            <span class="title_sub">{{ `地址${index}` }}</span>
            <van-field v-model="form.wallet_tag[index].address" placeholder="请输入地址" />
          </div>
          <div class="flex-start">
            <span class="title_sub">{{ `标签${index}` }}</span>
            <div class="flex-start" @click="handleTag(index)">
              {{
                form.wallet_tag[index].tag
                  ? wallet_tag_list_option?.find(i => i.value == this.form.wallet_tag[index].tag)
                      ?.text
                  : '选择'
              }}
              <van-icon name="arrow-down" />
            </div>
          </div>
          <div class="flex-start">
            <span class="title_sub">{{ `备注${index}` }}</span>
            <van-field v-model="form.wallet_tag[index].remark" placeholder="请输入备注" />
          </div>
          <div class="line"></div>
        </div>
      </div>
      <div class="footer">
        <div class="flex-between">
          <van-button type="default" style="width: 160px" @click="showEdit = false">
            取消
          </van-button>
          <van-button
            type="primary flex-1 ml-10"
            style="width: 160px; margin-left: 10px"
            native-type="submit"
          >
            确认
          </van-button>
        </div>
      </div>
    </van-form>
    <van-popup v-model:show="showCalendar" teleport="#app" position="bottom">
      <van-datetime-picker
        v-model="time"
        type="datetime"
        :min-date="minDate"
        :default-time="time"
        @confirm="onConfirmCalendar"
        @cancel="showCalendar = false"
      />
    </van-popup>
    <van-popup v-model:show="showTag" teleport="#app" position="bottom">
      <van-picker title="选择标签" :columns="wallet_tag_list_option" @confirm="onConfirmTag" />
    </van-popup>
    <van-dialog
      v-model:show="uploadDialogVisible"
      title="上传图片"
      show-cancel-button
      @confirm="getCropBlob"
      @cancel="uploadDialogVisible = false"
      confirm-button-text="确认"
      confirm-button-color="#3F80F7"
    >
      <div style="width: 100%; height: 300px">
        <vueCropper
          ref="cropper"
          :img="option.img"
          :outputSize="option.outputSize"
          :outputType="option.outputType"
          :maxImgSize="option.maxImgSize"
          :enlarge="option.enlarge"
          :high="option.high"
          :autoCrop="option.autoCrop"
          :autoCropWidth="option.autoCropWidth"
          :autoCropHeight="option.autoCropHeight"
          :fixed="option.fixed"
          :centerBox="option.centerBox"
          :canScale="option.canScale"
          :fixedBox="option.fixedBox"
        ></vueCropper>
      </div>
    </van-dialog>
  </van-popup>
</template>

<script>
import {
  getApplicationList,
  getConfigInfo,
  removeApplication,
  confirmApplication,
  updateApplication,
  submitLogo,
  getTokenInfo,
  getContractCheckResult
} from '@/api/index.js'
import 'vue-cropper/dist/index.css'
import { VueCropper } from 'vue-cropper'
export default {
  name: 'Audit',
  data() {
    return {
      id: '',
      tableData: [],
      domain: 'https://www.logofacade.com/',
      total: 0,
      pageNO: 1,
      pageSize: 10,
      dataLoading: false,
      finished: false,
      error: false,
      Timer: null,
      isSearch: false,
      showConfirm: false,
      remark: '',
      currentId: '',
      showEdit: false,
      form: {
        profile_picture: null,
        base_info: {
          symbol: '',
          token: '',
          chain: '',
          totol_supply: 0,
          website: '',
          // github: '',
          logoUrl: '',
          email: '',
          whitepaper: '',
          twitter: '',
          btok: '',
          qq: '',
          telegram: '',
          blog: '',
          reddit: '',
          slack: '',
          facebook: '',
          bitcointalk: '',
          wechat: '',
          linkedin: '',
          discord: '',
          description: '',
          intro_cn: '',
          intro_en: '',
          opening_at: 0
        },
        contract_info: {
          buy_tax: '',
          sell_tax: '',
          tm_sell_tax_for_fund: '',
          tm_buy_tax_for_burn: '',
          tm_sell_tax_for_burn: '',
          tm_buy_tax_for_lp: '',
          tm_sell_tax_for_lp: '',
          tm_buy_tax_for_team: '',
          tm_sell_tax_for_team: '',
          tm_buy_tax_for_holders: '',
          tm_sell_tax_for_holders: '',
          tm_buy_tax_for_lp_holders: '',
          tm_sell_tax_for_lp_holders: '',
          tm_buy_tax_for_other: '',
          tm_sell_tax_for_other: '',
          tm_max_hold_amount_per_wallet: '',
          mechanism_intro: ''
        },
        wallet_tag: [
          {
            address: '',
            tag: '',
            remark: ''
          }
        ]
      },
      rules: {
        'base_info.token': [{ required: true, message: '必填项', trigger: 'blur' }],
        'base_info.chain': [{ required: true, message: '必填项', trigger: 'blur' }]
      },
      value: [],
      showCalendar: false,
      time: '',
      minDate: new Date('2023-01-01'),
      showExpand1: false,
      showExpand2: false,
      wallet_tag_list_option: [
        { text: '营销地址', value: 'mabrket_address' },
        { text: '团队开发地址', value: 'team_address' },
        { text: '社区地址', value: 'community_address' },
        { text: '基金地址', value: 'fund_address' },
        { text: '挖矿地址', value: 'mining_address' }
      ],
      loadingRemove: [false],
      loadingAdd: false,
      showTag: false,
      indexTag: 0,
      editLoading: false,
      profile_picture: null,
      uploadDialogVisible: false,
      option: {
        img: '',
        outputSize: 0.2,
        outputType: 'png',
        maxImgSize: 200,
        enlarge: 0.5,
        high: false,
        autoCrop: true,
        autoCropWidth: '200',
        autoCropHeight: '200',
        fixed: true,
        centerBox: true,
        canScale: false,
        fixedBox: true
      },
      file: '',
      fileUrl: '',
      getCropBlobLoading: false,
      form2: {
        base_info: {
          symbol: '',
          token: '',
          chain: '',
          totol_supply: 0,
          website: '',
          // github: '',
          logoUrl: '',
          email: '',
          whitepaper: '',
          twitter: '',
          btok: '',
          qq: '',
          telegram: '',
          blog: '',
          reddit: '',
          slack: '',
          facebook: '',
          bitcointalk: '',
          wechat: '',
          linkedin: '',
          discord: '',
          description: '',
          intro_cn: '',
          intro_en: '',
          opening_at: 0
        }
      }
    }
  },
  components: {
    VueCropper
  },
  mounted() {
    // this.init()
  },
  methods: {
    search(val) {
      if (this.Timer) {
        clearTimeout(this.Timer)
      }
      this.Timer = setTimeout(() => {
        this.pageNO = 1
        this.tableData = []
        this.isSearch = true
        if (val) {
          console.log('this.searchObj.id', this.id)
          this.getApplicationList(this.id)
        } else {
          this.getApplicationList()
        }
      }, 1000)
    },
    onLoad() {
      console.log('onLoad')
      this.isSearch = false
      this.getApplicationList()
    },
    async init() {
      await this.getConfigInfo()
      this.getApplicationList()
    },
    getConfigInfo() {
      return new Promise((resolve, reject) => {
        getConfigInfo()
          .then(res => {
            const list = res || []
            this.domain = list.filter(i => i.name === 'Token_Logo_Url')[0]?.value || ''
            resolve(this.domain)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    getApplicationList(id) {
      this.dataLoading = true
      getApplicationList(this.pageNO, this.pageSize, id)
        .then(res => {
          console.log('-----tableData---------', res)
          this.total = res.Total || 0
          const list = res?.List?.map(i => {
            return {
              ...i,
              logo_url: i.base_info.logoUrl ? i.base_info.logoUrl : '',
              base_info: {
                ...i.base_info,
                logoUrl: i.base_info.logoUrl ? i.base_info.logoUrl : ''
              }
            }
          })
          this.tableData = [...this.tableData, ...list]
          this.finished = list?.length < this.pageSize
          this.pageNO++
        })
        .catch(() => {
          this.error = true
          this.tableData = []
        })
        .finally(() => {
          this.dataLoading = false
          if (this.showEdit) {
            this.showEdit = false
          }
        })
    },
    removeApplication(row) {
      this.$dialog
        .confirm({
          title: '提示',
          message: `该操作会删除${row._id}，是否继续？`,
          confirmButtonColor: '#3F80F7'
        })
        .then(() => {
          removeApplication(row._id).then(res => {
            this.$notify({ type: 'success', message: '删除成功' })
            row.is_deleted = true
          })
        })
        .catch(err => {
          console.log(err)
          this.$notify({ type: 'danger', message: err })
        })
    },
    handleConfirm(row) {
      this.currentId = row._id
      this.remark = row.admin_remark || 'yep'
      this.showConfirm = true
    },

    confirmApplication() {
      confirmApplication(this.currentId, this.remark)
        .then(res => {
          this.$notify({ type: 'success', message: '审核通过' })
          this.tableData = this.tableData.map(i => {
            if (i._id === this.currentId) {
              i.is_confirmed = true
            }
            return i
          })
        })
        .catch(err => {
          console.log(err)
          this.$notify({ type: 'danger', message: err })
        })
    },
    handleEdit(row) {
      this.showEdit = true
      this.form = {
        base_info: row.base_info,
        contract_info: row.contract_info,
        wallet_tag: row.wallet_tag
      }
      this.insertedID = row._id
      if (row.base_info.token && row.base_info.chain) {
        const tokenId = row.base_info.token + '-' + row.base_info.chain
        this.$nextTick(() => {
          this.getTokenInfo(tokenId)
          this.getContractCheckResult(tokenId)
        })
      }
    },
    handleCalendar() {
      this.showCalendar = true
      this.time = new Date(this.form.base_info.opening_at * 1000)
    },
    onConfirmCalendar(value) {
      this.showCalendar = false
      this.form.base_info.opening_at = Date.parse(value) / 1000
    },
    addWalletTag() {
      this.loadingAdd = true
      setTimeout(() => {
        this.form.wallet_tag.push({
          address: '',
          tag: '',
          remark: ''
        })
        this.loadingAdd = false
      }, 500)
    },
    removeWalletTag(index) {
      this.loadingRemove[index] = true
      setTimeout(() => {
        this.form.wallet_tag.splice(index, 1)
        this.loadingRemove[index] = false
      }, 500)
    },
    handleTag(index) {
      this.showTag = true
      this.indexTag = index
    },
    onConfirmTag(currentValue, currentIndex) {
      console.log(currentValue, currentIndex)
      this.form.wallet_tag[this.indexTag].tag = currentValue.value
      this.showTag = false
    },
    updateApplication() {
      this.editLoading = true
      updateApplication(this.insertedID, this.form)
        .then(res => {
          this.submitLogo(this.insertedID)
          setTimeout(() => {
            this.$notify({ type: 'success', message: '更新成功' })
            window.location.reload()
          }, 2000)
        })
        .catch(err => {
          console.log(err)
          this.$notify({ type: 'danger', message: err })
        })
        .finally(() => {
          setTimeout(() => {
            this.editLoading = false
          }, 2000)
        })
    },
    submitLogo(id) {
      if (this.profile_picture) {
        const formData = new FormData()
        const form = {
          profile_picture: this.profile_picture,
          id: id
        }
        console.log('this.profile_picture', this.profile_picture.file)
        Object.keys(form).forEach(key => {
          const value = form[key] ?? ''
          if (key === 'profile_picture' && value !== '') {
            formData.append('profile_picture', value, `${this.form.base_info.token}.png`)
          } else if (key !== 'symbol' && value !== '') {
            formData.append(key, value)
          }
        })
        console.log('this.formData', this.formData)
        submitLogo(formData).catch(err => {
          console.log(err)
        })
      }
    },
    beforeUpload(e) {
      const file = e.target.files[0]
      const fileUrl = window.URL.createObjectURL(file)
      this.option.img = fileUrl
      this.uploadDialogVisible = true
    },
    removeFile() {
      this.profile_picture = null
      this.fileUrl = ''
      if (this.$refs.fileLogo) {
        this.$refs.fileLogo.value = ''
      }
      this.option.img = ''
    },
    getCropBlob() {
      this.$refs.cropper.getCropBlob(data => {
        const file = new File([data], this.form.token_address)
        this.profile_picture = file
        this.fileUrl = window.URL.createObjectURL(file)
        this.uploadDialogVisible = false
      })
    },
    getTokenInfo(tokenId) {
      this.loading = true
      getTokenInfo(tokenId)
        .then(res => {
          this.form2.base_info.symbol = res?.name_space?.symbol
          this.form2.base_info.token = res?.token
          this.form2.base_info.chain = res?.chain
          this.form2.base_info.totol_supply = Number(res?.name_space?.total)

          this.form2.base_info.website = res?.name_space?.website
          this.form2.base_info.github = res?.community_space?.github
          this.form2.base_info.email = res?.community_space?.email
          this.form2.base_info.whitepaper = res?.name_space?.white_paper
          this.form2.base_info.twitter = res?.name_space?.twitter
          this.form2.base_info.twitter = res?.community_space?.twitter
          this.form2.base_info.btok = res?.community_space?.btok
          this.form2.base_info.qq = res?.community_space?.qq
          this.form2.base_info.telegram = res?.community_space?.telegram
          this.form2.base_info.blog = res?.community_space?.blog
          this.form2.base_info.reddit = res?.community_space?.reddit
          this.form2.base_info.slack = res?.community_space?.slack
          this.form2.base_info.facebook = res?.community_space?.facebook
          this.form2.base_info.bitcointalk = res?.community_space?.bitcointalk
          this.form2.base_info.wechat = res?.community_space?.wechat
          this.form2.base_info.linkedin = res?.community_space?.linkedin
          this.form2.base_info.discord = res?.community_space?.discord
          this.form2.base_info.description = res?.description
          this.form2.base_info.intro_cn = res?.intro_cn
          this.form2.base_info.intro_cn = res?.intro_en
          this.form2.base_info.opening_at = res?.opening_at
        })
        .catch(() => {
          this.form.symbol = ''
        })
        .finally(() => {
          this.loading = false
        })
    },
    getContractCheckResult(tokenId) {
      // const id = '0xe9705f62deee16e4fbb8e4311fbbcacf9fabdb08-bsc'
      getContractCheckResult(tokenId)
        .then(res => {
          res.forEach(i => {
            this.form2.contract_info[i.key] = i.value
          })
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>

